import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ProductsProvider } from '../../providers/products/products';

@IonicPage()
@Component({
  selector: 'page-shop',
  templateUrl: 'shop-products.html',
})
export class ShopProductsPage {
  productDetails: any = [];
  productId: any;

  constructor(public navCtrl: NavController, public navParams: NavParams, private productsProvider: ProductsProvider) {
    this.productId = '13122352';
    // this.productId = `13122354`;
    this.getProductList()
   
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ShopPage');
    
  }

  goto(page){
    this.navCtrl.push(page, { productDetails: this.productDetails });
  }

  getProductList(){
    this.productsProvider.fetchProductDetails(this.productId).subscribe(productDetails => {
      console.log(productDetails['Product'],'=====================Product Details=====================');
      // this.productDetails = productDetails['Product'];
      this.productDetails.push(productDetails['Product']);
      
    });
  }

}



